import { Component, OnInit } from '@angular/core';
import { Socket } from 'ngx-socket-io';
import { Router } from "@angular/router"
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { ToastController } from '@ionic/angular';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage implements OnInit {
  joinRoomId: string;
  username: string = "";
  password: string = "";
  user: string = "";

  constructor(private socket: Socket, private router: Router, private http: HttpClient, private toastController: ToastController, private ds: DataService) { }

  ngOnInit() {
    this.socket.connect();
    this.user = localStorage.getItem('username');
  }

  logout() {
    this.user = "";
    localStorage.removeItem('username');
    this.ds.setUsername("");
  }

  register() {
    if (!this.username || !this.password) {
      this.presentToastWithOptions({ message: "Username and password required.", status: "danger" });
      return;
    }
    this.http.post(`${environment.SERVER_URL}/user`, { user: { username: this.username, password: this.password } }).subscribe((data: { message: string, status: string }) => {
      if (data.status !== 'danger') {
        this.ds.setIsLogged(true);
        this.ds.setUsername(this.username);
        localStorage.setItem('username', this.username);
        this.user = this.username;

        this.username = "";
        this.password = "";
        this.presentToastWithOptions(data);
      }
      else
        this.presentToastWithOptions({ message: "Username already taken.", status: "danger" });
    });
  }

  login() {
    if (!this.username || !this.password) {
      this.presentToastWithOptions({ message: "Username and password required.", status: "danger" });
      return;
    }
    this.http.get(`${environment.SERVER_URL}/user/${this.username}`)
      .subscribe((data: any) => {
        if (data) {
          if (data.password === this.password) {
            this.ds.setIsLogged(true);
            this.ds.setUsername(this.username);
            localStorage.setItem('username', this.username);
            this.user = this.username;
            this.username = "";
            this.password = "";
            return;
          }
        }
        this.presentToastWithOptions({ message: "Username doesn't exist.", status: "danger" });
      });
  }

  createRoom() {
    let roomId = this.makeid(5);
    this.http.post(`${environment.SERVER_URL}/playlist`, { item: { title: roomId, description: 'test description', user: this.ds.getUsername() } })
      .subscribe(data => {
        if (data) {
          this.presentToastWithOptions(data);
          this.router.navigate(['/tv', { id: roomId }])
        }
      });
  }

  joinRoom() {
    if (this.joinRoomId) {
      this.http.get(`${environment.SERVER_URL}/playlists`)
        .subscribe((data: any) => {
          console.log(data)
          if (data.filter(item => item.title === this.joinRoomId).length)
            this.router.navigate(['/user', { id: this.joinRoomId }])
        });
    }
  }

  makeid(length) {
    var result = '';
    var characters = 'ABCDEFGHIJKLMNOPQRSTUVWXZY0123456789';
    for (var i = 0; i < length; i++) {
      result += characters.charAt(Math.floor(Math.random() * characters.length));
    }
    return result;
  }

  async presentToastWithOptions(data) {
    const toast = await this.toastController.create({
      message: data["message"],
      position: 'bottom',
      duration: 2000,
      color: data["status"],
      buttons: [
        {
          side: 'start',
          icon: 'musical-notes',
        },
        {
          icon: 'close',
          role: 'cancel'
        }
      ]
    });
    toast.present();
  }
}